import React, { lazy } from "react";
import { withTranslation, Trans } from "react-i18next";
import cook from "../assets/Cook-Stake-Earn/cook.svg";
import stake from "../assets/Cook-Stake-Earn/stake.svg";
import earn from "../assets/Cook-Stake-Earn/earn.svg";
const HowItWorksWeb = lazy(() => import("./WebComponents/HowItWorksWeb"));
const HowItWorksMobile = lazy(() =>
  import("./MobileComponents/HowItWorksMobile")
);

const HowItWorksComponent = () => {
  const steps = [
    {
      image: cook,
      number: "1",
      title: <Trans>{"howItWorksComponent.cook.title"}</Trans>,
      content: <Trans>{"howItWorksComponent.cook.text"}</Trans>,
    },
    {
      image: stake,
      number: "2",
      title: <Trans>{"howItWorksComponent.stake.title"}</Trans>,
      content: <Trans>{"howItWorksComponent.stake.text"}</Trans>,
    },
    {
      image: earn,
      number: "3",
      title: <Trans>{"howItWorksComponent.earn.title"}</Trans>,
      content: <Trans>{"howItWorksComponent.earn.text"}</Trans>,
    },
  ];
  return (
    <>
      <HowItWorksWeb steps={steps} display={{ xs: "none", md: "flex" }} />
      <HowItWorksMobile steps={steps} display={{ xs: "flex", md: "none" }} />
    </>
  );
};
export default withTranslation()(HowItWorksComponent);
